.button {
  background-color: transparent;
  border: none;
  padding: 8px 16px;
  font-size: $font-size-base;
  cursor: pointer;
  text-decoration: none;
  display: inline-flex;
}

.button--primary {
  background-color: $primary;
  color: $white;
}

.button--secondary {
  background-color: $secondary;
  color: $white;
}
